import Vue from 'vue';
import Router from 'vue-router';
import Home from '../components/Home';
import ourSolution from '../components/ourSolution';
import Security from '../components/Security';
import Ecosystem from '../components/Ecosystem';


Vue.use(Router);

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home,
    },
    {
      path: '/daemtech',  // route added for deploy on github
      name: 'Home',
      component: Home,
    },
    {
      path: '/our-solution',
      name: 'ourSolution',
      component: ourSolution,
    },
    {
      path: '/security',
      name: 'Security',
      component: Security,
    },
    {
      path: '/ecosystem',
      name: 'Ecosystem',
      component: Ecosystem,
    },
  ],
});



